<template>
    <div class="recipe-details" v-if="recipe.nom">
      <h2>{{ recipe.nom }}</h2>
      <img :src="recipe.image" alt="Recipe Image" class="recipe-image">
      <p>{{ recipe.description }}</p>
      <div class="details">
        <p v-if="recipe.tempsPreparation"><strong>Temps de préparation:</strong> {{ recipe.tempsPreparation }}</p>
        <p v-if="recipe.tempsCuisson"><strong>Temps de cuisson:</strong> {{ recipe.tempsCuisson }}</p>
        <p v-if="recipe.niveauDifficulte"><strong>Niveau de difficulté:</strong> {{ recipe.niveauDifficulte }}</p>
        <p v-if="recipe.auteur"><strong>Auteur:</strong> {{ recipe.auteur }}</p>
        <p v-if="recipe.typeDeRegime"><strong>Type de régime:</strong> {{ recipe.typeDeRegime }}</p>
        <p v-if="recipe.categorie"><strong>Catégorie:</strong> {{ recipe.categorie }}</p>
        <p v-if="recipe.popularite"><strong>Popularité:</strong> {{ recipe.popularite }} / 5</p>
      </div>
      <h3>Ingrédients</h3>
      <ul class="ingredients-list">
        <li v-for="(ingredient, index) in recipe.ingredients" :key="index">
          {{ ingredient.nom }} - {{ ingredient.quantité }} {{ ingredient.unité }}
        </li>
      </ul>
      <h3>Instructions</h3>
      <ol class="instructions-list">
        <li v-for="(instruction, index) in recipe.instructions" :key="index">{{ instruction }}</li>
      </ol>
    </div>
  </template>
  
  <script>
  import firebase from 'firebase/app';
  import 'firebase/firestore';
  
  const db = firebase.firestore();
  
  export default {
    name: 'RecipeDetails',
    props: {
      id: String,
    },
    data() {
      return {
        recipe: {},
      };
    },
    async created() {
      try {
        const doc = await db.collection('recipes').doc(this.id).get();
        if (doc.exists) {
          this.recipe = doc.data();
          // Ensure arrays are correctly formatted
          
        } else {
          console.error('No such document!');
        }
      } catch (error) {
        console.error('Error getting document:', error);
      }
    },
  };
  </script>
  
  <style scoped>
  /* CSS styles for the recipe details */
  .recipe-details {
    max-width: 800px;
    margin: auto;
    padding: 20px;
  }
  
  .recipe-image {
    width: 100%;
    height: auto;
    margin-bottom: 20px;
  }
  
  .recipe-details h2 {
    margin-bottom: 20px;
    font-size: 2rem;
    color: #34495e;
  }
  
  .recipe-details p {
    font-size: 1.2rem;
    color: #666;
  }
  
  .details p {
    margin-bottom: 10px;
  }
  
  .ingredients-list,
  .instructions-list {
    list-style-type: disc;
    padding-left: 20px;
  }
  
  .instructions-list {
    list-style-type: decimal;
  }
  
  .ingredients-list li,
  .instructions-list li {
    margin-bottom: 10px;
    font-size: 1rem;
    color: #666;
  }
  </style>
  